 import { useState ,useEffect} from "react";
import axios from "axios";
import { LoginPage } from "./login";
import Card from '@mui/material/Card';
import { useDispatch, useSelector } from 'react-redux';
import { addToSaveList } from './slicer/count-slicer';
import {UserReg} from './registration';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import Modal from 'bootstrap/js/dist/modal';

 // <--- This line fixes the "bootstrap is not defined" error



import {BrowserRouter,Routes,Route, useNavigate} from 'react-router-dom';
import { useCookies } from "react-cookie";







 export function Binding()
{
  const[products,setProducts] = useState([]);
  const[cartItems,setCartItems] = useState([]);
  const[totalAmount,settotalAmount] = useState(0);
  const dispatch = useDispatch();
  const count = useSelector((state) => state.count.count);
  const navigate =  useNavigate();

  const[Cookie,setCookie, removeCookie] = useCookies();

  

   useEffect(()=>{

    axios.get("https://fakestoreapi.com/products")
    .then((res)=>{
           console.log(res.data); 

            setProducts(res.data);
          

    })

},[])
useEffect(() => {
  const loadRazorpayScript = () => {
    return new Promise((resolve) => {
      if (window.Razorpay) {
        resolve(true);
      } else {
        const script = document.createElement('script');
        script.src = 'https://checkout.razorpay.com/v1/checkout.js';
        script.async = true;
        script.onload = () => resolve(true);
        script.onerror = () => resolve(false);
        document.body.appendChild(script);
      }
    });
  };

  loadRazorpayScript().then((success) => {
    if (!success) {
      console.error('Failed to load Razorpay SDK');
      alert('Failed to load Razorpay SDK. Check your internet connection or try again later.');
    }
  });
}, []);



  function fetchProducts(url)
{
         

            axios.get(url)
            .then((res)=>{
                setProducts(res.data);
            })

         
}


    

function navClick(e)
{  
    e.preventDefault();
    let category = e.target.getAttribute("data-category");

    console.log(category);

    fetchProducts(`https://fakestoreapi.com/products/category/${category}`);


}

function addCart(id)
{  
     alert(id);
     axios.get(`https://fakestoreapi.com/products/${id}`)
    .then((res)=>{
        console.log("Product added to cart:", res.data);
        setCartItems((prev)=>[...prev,res.data]);
       
         dispatch(addToSaveList(1));

       

       

    })
}

function deleteClick(id)
{
    console.log(id);
   const UpdatedCart  = cartItems.filter((item)=>item.id!==id);
    setCartItems(UpdatedCart);
     dispatch(addToSaveList(-1));

}
function payNow() {
  const amount = totalAmount * 100; // Convert to paisa

  axios.post("http://localhost:9099/SpringRestProj17-RazorpayPayments-2/api/payment/createOrder", {
    CustomerAmount: amount
  }, {
    headers: {
      'Content-Type': 'application/json'
    }
  })
  .then((res) => {
    console.log("Order response:", res.data);
    const order = res.data; // Use directly, no parsing needed

    const options = {
      key: "rzp_test_TInZzb0i7J7jgH",
      amount: order.amount,
      currency: order.currency,
      name: "FakeStore",
      description: "Test Transaction",
      order_id: order.id,
      handler: function (response) {
        alert("Payment Successful! Payment ID: " + response.razorpay_payment_id);

        //Send payment success details to backend
        axios.post("http://localhost:9099/SpringRestProj17-RazorpayPayments-2/api/payment/paymentSuccess", {
          orderId: order.id,
          paymentId: response.razorpay_payment_id,
          amount: order.amount,
          user_name: Cookie.user || "Guest"
        });

      },
      theme: {
        color: "#3399cc"
      },
      prefill: {
        name: Cookie.user || "Guest",
        email: "guest@example.com",
        contact: "9999999999"
      }
    };

    const rzp = new window.Razorpay(options);
    if (typeof rzp.open === 'function') {
      rzp.open();
    } else {
      console.error("Razorpay failed to initialize");
    }
  })
  .catch((err) => {
    console.error("Payment error:", err.response?.data || err.message);
    console.error("Full error:", err);
    alert("Payment failed. Check console for details.");
  });
}

useEffect(()=>{
 settotalAmount(cartItems.reduce((sum,item)=>sum+item.price,0));

},[cartItems])


     



return(
    <div className="mt-3">
      
            
        
           
            <div className=" p-3 bg-dark text-white ">
     
        <nav className=" container d-flex justify-content-between align-items-center">
            <div>
                <h1>FakeStore</h1>
            </div>
            <div className="">
                  <input type="text" className="form-control" placeholder="Search...." />
            </div>
           
          
          <div className=" text-white text-decoration-none ">
            
           
            <a  href="#" data-category="electronics" onClick={navClick} className="mx-5 text-white text-decoration-none   fw-bold">Electronics</a>
             < a href="#" data-category="jewelery"onClick={navClick} className="mx-5 text-white text-decoration-none  fw-bold">Jewelery</a>
              < a href="#"data-category="men's clothing" onClick={navClick} className="mx-5 text-white text-decoration-none  fw-bold">Men's clothing</a>
               < a href="#" data-category ="women's clothing" onClick={navClick} className="mx-5 text-white text-decoration-none   fw-bold">Women's clothing</a>
                <button className=" btn btn-light" data-bs-toggle="modal" data-bs-target="#cart"> <span className="bi bi-cart"></span></button>
                    <div className="modal fade" id="cart">
                        <div className="modal-dialog">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h1 className="text-primary">Your Cart Items</h1>
                                    <button className="btn btn-close" data-bs-dismiss="modal"></button>

                                </div>
                                <div className="modal-body">
                                    <table className="table table-hover">
                                        <thead>
                                            <tr>
                                                <th>category</th>
                                                <th>Preview</th>
                                                <th>Price</th>
                                                <th>Remove</th>
                                            </tr>
                                            
                                        </thead>
                                        <tbody>
                                            {
                                                cartItems.map((res)=>
                                            <tr>
                                                <td className="fs-5 fw-bold">{res.category}</td>
                                                 <td> <img src={res.image} height="70"  width="140" alt={res.title}/></td>
                                                 <td className="fs-5 fw-bold">{res.price}</td>
                                                 <button className="btn btn-danger  btn-50 bi bi-trash-fill" onClick={()=>deleteClick(res.id)}>Remove</button>

                                             
                                                
                                            </tr>
                                            )
                                            }
                                            <div className="mt-4   d-flex justify-content-end" >
                                                <tr className="border border-1 rounded p-3 ">
                                                    <span className="">TotalItems{count}</span>
                                                    <td> <span className="text-primary col-12  text-end fw-bold colSpan=2 ">TotalPrice:{totalAmount}</span></td>
                                                    <button className="btn btn-success w-100 mt-3" onClick={payNow}><i className="bi bi-cash-coin"></i> Pay Now
</button>

                                                    
                                                   
                                                </tr>
                                            </div>
                                        </tbody>
                                        </table>
               </div>
               </div>
               </div>
               </div>
            </div>
                
               

        </nav>

        </div>
        <h3 className="text-end mx-3">{Cookie.user}</h3>
     
        <main className=" mt-5 d-flex  flex-wrap gap-3 container "  >
                {
                    products.map((product)=>

                      
                        <div className="card overflow-auto" style={{ width: '18rem', height: '350px' }} >
                            <div className="card-header d-flex justify-content-center">
                       <img src={product.image} alt={product.id} height="120"/>

                        </div>
                        <div className="card-body">
                             <h5>{product.title}</h5>
                            <ul className="list-unstyled">
                                <li>brand:{product.brand}</li>
                                <li>model:{product.model}</li>
                                <li>color:{product.color}</li>
                                <li>price:{product.price}</li>
                                
                                <li>category:{product.category}</li>
                                <li>rating:<i className="bi bi-star-fill text-success"></i>
                                           <i className="bi bi-star-fill text-success"></i>
                                           <i className="bi bi-star-fill text-success"></i>
                                           <i className="bi bi-star-fill text-success"></i></li>
                            </ul>

                        </div>
                        <div className="card-footer">
                            <button className="btn w-100  btn-warning"    onClick={()=> addCart(product.id)}><i className="bi bi-cart"></i>AddToCart</button>

                        </div>

                        </div>
                        
                    )
                }

            

        
   </main>
   
    
    </div>
)
}