import { TextField,Button} from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import { useCookies } from 'react-cookie';
import axios from 'axios';
import { useState } from 'react';
export function LoginPage()
{
     const navigate = useNavigate();

     const[Cookie,setCookie, removeCookie] = useCookies();
     const[userName,setUserName] = useState();
     const[psword,setPword] = useState();

     function handleSubmit(e)
     {
       axios.post("http://localhost:9099/SpringRestProj17-RazorpayPayments-2/api/payment/loginPage",{
        name:userName,
        password:psword}).then(()=>{
        navigate("/home");
     })}
     function handleChange(e)
     {
         setCookie("user",e.target.value);
        
         setUserName(e.target.value);
        
     }
     function handleChange2(e)
     {
        
       
         setPword(e.target.value);
        
     }


    return(
        <div className="container-fluid d-flex justify-content-center mt-5 align-items-center  " style={{ height: '60vh' }}>
            <div className="  w-25  p-5" style={{border:'1px double  orange'}} >
            <h3 className="bi bi-person-fill text-center ">Login Here</h3>
           <TextField  label="UserName" onChange={handleChange}variant="standard" fullWidth name="name" /><br></br>
             <TextField  label="Password" variant="standard" onChange={handleChange2} fullWidth name="password"/><br></br>
             
             <Button variant="outlined" fullWidth className="mt-3" onClick={handleSubmit}> Submit</Button>
              <div className="mt-3">
              Don't have an account?<Link to="/reg" >Sign Up</Link>
              </div>
           </div>
          
        </div> 
    )
}