  import { createSlice } from "@reduxjs/toolkit";

  const initialState = {
    count: 0,
  };

  const countSlice = createSlice({
    name: "count",
    initialState,
    reducers: {
      addToSaveList: ((state, action) => {
        state.count += action.payload;
      }),
    },
  });

  export const { addToSaveList } = countSlice.actions;
  export default countSlice.reducer; //  export only the reducer
