import { TextField,Button} from '@mui/material';
import { Box } from '@mui/system';
import axios from 'axios';
import { useFormik } from 'formik';
import { useNavigate } from 'react-router-dom';
import * as yup from "yup";
export  function UserReg()
{

    const navigate = useNavigate();
    
    const formik  = useFormik({
        initialValues:{
            
            fname:'',
            lname:'',
            pword:'',
            cword:'',


        },
        validationSchema: yup.object({
            
            fname:yup.string().required('First Name is required').min(4),
            lname:yup.string().required('Last Name is required').min(4),
            pword:yup.string().required('Password is required').min(4),
            cword:yup.string().required('ConfirmPassword is required').min(4),



    }),

        


        onSubmit:(values)=>{
            alert(values);

          axios.post("http://localhost:9099/SpringRestProj17-RazorpayPayments-2/api/payment/signUpPage",{
            fistName:formik.values.fname,
            lastName:formik.values.lname,
            password:formik.values.pword,
            confirm:formik.values.cword,
          


        }).then(()=>{
              
          navigate('/')})

          .catch(()=>{
            console.error("Error during API call");
        alert("Registration failed. Check console.");
          })
        



        }
    });

    function handleSubmit()
    {
        
    }

    return(


        <div className="container-fluid mt-5 p-4 w-25" >
         
            
        <Box sx={{ p: 2,m: 1,border: '1px solid purple',borderRadius: '16px',textAlign: 'center',  }}>
           <h3 className="bi bi-person">UserRegistration</h3>
           <form onSubmit={formik.handleSubmit}>
          <TextField label="FirstName" variant="standard" fullWidth name="fname" onChange={formik.handleChange}  /> 
          <span className="text-danger">{formik.errors.fname}</span>
           
        <TextField label="LastName" variant="standard" fullWidth  name="lname" onChange={formik.handleChange} />
         <span className="text-danger">{formik.errors.lname}</span><br></br>
        
        
        <TextField label="Password" variant="standard" fullWidth  name="pword" onChange={formik.handleChange}/>
        <span className="text-danger">{formik.errors.pword}</span>
        <TextField label="ConfirmPassword" variant="standard" fullWidth  name="cword" onChange={formik.handleChange}/>
        <span className="text-danger">{formik.errors.cword}</span><br></br>
          <Button variant="contained" className="mt-3" fullWidth  onClick={formik.handleSubmit}>Submit</Button>
        </form>
        </Box>
      
      
      
      

        </div>
    )
}